#videos {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  flex-direction: column;
  padding-right: 12px;
  padding-left: 12px;
  justify-content: center;
  align-items: center;
}

.videos__top--info {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;
}

.videos__top--para {
  color: white;
  font-size: 24px;
  width: 100%;
  padding: 0 12px;
}

.videos__top--bold {
  font-size: 42px;
  font-weight: 500;
}

.videos--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 12px;
  width: 100%;
}

.video {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(14, 14, 14);
  width: 100%;
  max-width: 900px;
  padding: 12px 12px;
  margin: 12px 0;
  text-decoration: none;
  transition: all 500ms ease;
}

.video:hover {
  transform: translateY(-8px);
}

.video__img--wrapper {
  width: 330px;
  height: 185px;
  overflow: hidden;
  margin: 0;
  margin-right: 28px;
}

.video__img {
  width: 100%;
}

.video__info {
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: start;
}

.video__info-top {
  display: flex;
  flex-direction: column;
}

.video__title {
  font-size: 36px;
  color: white;
  margin: 0;
}

.video__creator {
  color: rgb(219, 219, 219);
  padding-bottom: 20px;
  margin: 0;
  font-weight: 500;
}

.video__date {
  margin: 0;
  color: rgb(243, 26, 26);
  font-size: 20px;
  font-weight: 500;
}

.videos__see-more {
  color: rgb(194, 194, 194);
  text-align: center;
  cursor: pointer;
  font-weight: 400;
  transition: all 300ms ease;
  font-size: 16px;
}

.videos__see-more:hover {
  color: white;
  transform: scale(1.05);
}