nav {
  height: 250px;
}

.nav--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 50px;
}

.header__logo--wrapper {
  width: 350px;
  margin: 0;
}

.header__logo {
  width: 100%;
}

.header__links {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__link {
  color: rgb(194, 194, 194);
  font-size: 22px;
  padding: 4px 0;
  transition: all 300ms ease;
}

.header__link:hover {
  color: white;
  padding-right: 4px;
  cursor: pointer;
}