@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&family=Rubik:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #050505;
}
nav {
  height: 250px;
}

.nav--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 50px;
}

.header__logo--wrapper {
  width: 350px;
  margin: 0;
}

.header__logo {
  width: 100%;
}

.header__links {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__link {
  color: rgb(194, 194, 194);
  font-size: 22px;
  padding: 4px 0;
  transition: all 300ms ease;
}

.header__link:hover {
  color: white;
  padding-right: 4px;
  cursor: pointer;
}
.each-slide-effect > div {
  height: 90vh;
  background-size: cover;
  background-position: top;
  display: flex;
  justify-content: flex-end;
  align-items: end;
}

.image__info {
  z-index: 200 !important;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-end;
  width: 400px;
  margin-bottom: 40px;
  margin-right: 60px;
}

.image__quote,
.image__person {
  color: white;
  font-family: "Roboto";
  z-index: 205 !important;
  margin: 6px 0;
  text-align: end;
}

.image__quote {
  font-size: 56px;
  font-weight: 600;
}

.image__person {
  font-size: 32px;
  font-weight: 400;
}

.default-nav {
  background-color: transparent !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  border-radius: 0 !important;
  transition: all 300ms ease;
  margin: 0 12px;
  -webkit-transform: scale(1.6);
          transform: scale(1.6);
}

.default-nav:hover {
  -webkit-transform: scale(1.8);
          transform: scale(1.8);
}

.default-nav svg path {
  fill: white !important;
  transition: all 300ms ease;
}

.default-nav:hover svg path {
  fill: rgb(199, 199, 199) !important;
}
#videos {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  flex-direction: column;
  padding-right: 12px;
  padding-left: 12px;
  justify-content: center;
  align-items: center;
}

.videos__top--info {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;
}

.videos__top--para {
  color: white;
  font-size: 24px;
  width: 100%;
  padding: 0 12px;
}

.videos__top--bold {
  font-size: 42px;
  font-weight: 500;
}

.videos--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 12px;
  width: 100%;
}

.video {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(14, 14, 14);
  width: 100%;
  max-width: 900px;
  padding: 12px 12px;
  margin: 12px 0;
  text-decoration: none;
  transition: all 500ms ease;
}

.video:hover {
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

.video__img--wrapper {
  width: 330px;
  height: 185px;
  overflow: hidden;
  margin: 0;
  margin-right: 28px;
}

.video__img {
  width: 100%;
}

.video__info {
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: start;
}

.video__info-top {
  display: flex;
  flex-direction: column;
}

.video__title {
  font-size: 36px;
  color: white;
  margin: 0;
}

.video__creator {
  color: rgb(219, 219, 219);
  padding-bottom: 20px;
  margin: 0;
  font-weight: 500;
}

.video__date {
  margin: 0;
  color: rgb(243, 26, 26);
  font-size: 20px;
  font-weight: 500;
}

.videos__see-more {
  color: rgb(194, 194, 194);
  text-align: center;
  cursor: pointer;
  font-weight: 400;
  transition: all 300ms ease;
  font-size: 16px;
}

.videos__see-more:hover {
  color: white;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
footer {
  background-color: white;
  width: 100%;
  padding: 18px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
}

.footer__link {
  color: black;
}

.icon {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  padding: 0 18px;
  transition: all 250ms ease;
  cursor: pointer;
}

.icon:hover {
  fill: rgb(63, 63, 63);
}
